<template>
<div class="dashboard ma-5">
  <h1 class="subheading grey--text">Receipt Generator</h1>
  <v-container>
    <v-card class="pa-9">
      <v-layout row wrap justify-space-between>
        <v-flex xs12 sm12 md12 lg6 xl6>
          <div class="caption grey--text text-center">Input</div><br>
          <v-form id="receipt">
            <!-- <v-select :items="items" label="Font Style" solo></v-select> -->
            <v-layout>
              <v-flex lg3>
                <v-select :items="font" v-model="selectedFont" solo></v-select>
              </v-flex>
              <v-flex lg12>
                <v-text-field label="Business Name/Enter your own text" v-model="businessName" solo></v-text-field>
              </v-flex>
            </v-layout>
            <v-text-field label="Address/Enter your own text" v-model="address" solo></v-text-field>
            <v-text-field label="City,State/Enter your own text" v-model="cityState" solo></v-text-field>
            <v-text-field label="Phone Number/Enter your own text" v-model="phone" solo></v-text-field>

            <v-menu v-model="menu2" :close-on-content-click="false" :nudge-right="40" lazy transition="scale-transition" offset-y full-width min-width="290px">
              <template v-slot:activator="{ on }">
                <v-text-field v-model="date" label="Date" prepend-icon="event" readonly v-on="on"></v-text-field>
              </template>
              <v-date-picker v-model="date" @input="menu2 = false"></v-date-picker>
            </v-menu>

            <v-menu ref="menu" v-model="menu3" :close-on-content-click="false" :nudge-right="40" :return-value.sync="time" lazy transition="scale-transition" offset-y full-width max-width="290px" min-width="290px">
              <template v-slot:activator="{ on }">
                <v-text-field v-model="time" label="Time" prepend-icon="access_time" readonly v-on="on"></v-text-field>
              </template>
              <v-time-picker v-if="menu3" v-model="time" full-width @click:minute="$refs.menu.save(time)"></v-time-picker>
            </v-menu>

            <v-layout>
              <v-flex lg3>
                <v-select :items="currency" v-model="selectedCurrency" solo></v-select>
              </v-flex>
              <v-flex lg12>
                <v-text-field label="Cost" v-model="cost" solo></v-text-field>
              </v-flex>
            </v-layout>
            <v-layout>
              <v-flex lg3>
                <v-select v-model="selectedTax" :items="tax" item-text="taxType" item-value="taxLabel" persistent-hint return-object single-line solo></v-select>
              </v-flex>
              <v-flex lg12>
                <v-text-field label="For e.g. 7" v-model="taxPercent" solo></v-text-field>
              </v-flex>
            </v-layout>
            <v-text-field label="Last 4 digits of card/Enter your own text" v-model="payment" :rules="paymentRules" solo></v-text-field>
            <v-text-field label="Add Custom Text/Enter your own text" v-model="customText" solo></v-text-field>
          </v-form>
        </v-flex>
        <v-flex xs12 sm12 md12 lg6 xl6>
          <div class="caption grey--text text-center">Receipt</div><br>
          <v-card class="mx-auto pa-12 grey lighten-5 font-weight-medium" id="receipt" :flat="flat" :loading="loading" :outlined="outlined" :elevation="elevation" :raised="raised" width="400px" height="600px">
            <v-card-text class="py-1 text-center"><span class="font-weight-black" v-if="selectedFont='Bold'">{{businessName}}</span></v-card-text>
            <v-card-text class="py-1 text-center">{{address}}</v-card-text>
            <v-card-text class="py-1 text-center">{{cityState}}</v-card-text>
            <v-card-text class="py-1 text-center">{{phone}}</v-card-text>
            <v-card-text class="py-1"><span class="text-left">{{date}}</span><span class="float-right">{{time}}</span></v-card-text>
            <v-card-text class="py-3 text-center">SALE</v-card-text>
            <v-card-text class="py-1"><span class="text-left">AMOUNT</span><span class="float-right"><span v-if="selectedCurrency!=='None'">{{selectedCurrency}} </span>{{parseFloat(cost).toFixed(2)}}</span></v-card-text>
            <v-card-text class="py-1" v-if="selectedTax.taxType!=='No Tax'"><span class="text-left">{{selectedTax.taxLabel}}</span><span class="float-right"> <span v-if="selectedCurrency!=='None'">{{selectedCurrency}} </span>{{(cost*taxPercent/100).toFixed(2)}}</span></v-card-text>
            <v-card-text class="py-1"><span class="text-left">TOTAL</span><span class="float-right"><span v-if="selectedCurrency!=='None'">{{selectedCurrency}} </span>{{(parseFloat(cost)+parseFloat(cost*taxPercent/100)).toFixed(2)}}</span></v-card-text>
            <v-card-text class="pt-3 text-center">Payment: {{payment}}</v-card-text>
            <v-card-text class="py-1"><span class="text-left">AUTH # {{auth}}</span><span class="float-right">APPR CODE: {{approve}}</span></v-card-text>
            <v-card-text class="py-1 text-center">APPROVED</v-card-text>
            <v-card-text class="py-3 text-center">{{customText}}</v-card-text>
          </v-card>
        </v-flex>
      </v-layout>
    </v-card>
  </v-container>
</div>
</template>

<script>
export default {
  data() {
    return {
      selectedFont:"Bold",
      font:["Bold","None"],
      date: new Date().toISOString().substr(0, 10),
      menu: false,
      modal: false,
      time: new Date().toTimeString().substr(0, 5),
      menu2: false,
      modal2: false,
      menu3: false,
      businessName:null,
      address:null,
      cityState:null,
      phone:null,
      selectedCurrency:"$",
      currency:["$","€","£","¥","฿","Rs","DHS","HK$","SG$","R$","None"],
      cost:0,
      selectedTax:{taxType:"Tax %",taxLabel:"TAX"},
      tax:[
        {taxType:"Tax %",taxLabel:"TAX"},
        {taxType:"Additional %",taxLabel:"ADDITIONAL"},
        {taxType:"Tip %",taxLabel:"TIP"},
        {taxType:"VAT %",taxLabel:"VAT"},
        {taxType:"Sales Tax %",taxLabel:"SALESTAX"},
        {taxType:"Service %",taxLabel:"SERVICE"},
        {taxType:"No Tax",taxLabel:""}
      ],
      taxPercent:0,
      auth:Math.floor(Math.random()*9000) + 1000,
      approve:Math.floor(Math.random()*90000000) + 10000000,
      payment:null,
      customText:null,
      paymentRules: [
        v => v.length == 4 || v.length == 0 || 'Must be 4 digits'
      ],
    }
  }
}
</script>
