import Vue from 'vue'
import Router from 'vue-router'
import Navbar from './views/Navbar'
import Home from './views/Home'
import About from './views/About'
import Example from './views/Example'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/about',
      name: 'About',
      component: About
    },
    {
      path: '/example',
      name: 'Example',
      component: Example
    },
  ]
})
